export const Logo = () =>{


    return `
    

    
        <div class="LogoMobile screen-container">

            <div class="LogoMobile__bgCircle">
                <img loading="lazy" width="100%" src="/assets/bg-circle-1.png" alt="fondo logo glowing phoenix" srcset="">
            </div>

            <div class="LogoMobile__logo">
                <img width="100%" src="/assets/ave-logo.webp" alt="logo glowing phoenix banda de metal" srcset="">
            </div>

            <div class="LogoMobile__text">
                <p>nete a esta experiencia de otro mundo y haz un viaje metalero con nuestro hit musical</p>
            </div>

            <div class="LogoMobile__button">
                <div class="d-grid gap-2">
                    <button class="btn btn-danger my-btn-index-mobile  watch-video-now" type="button">
                        <strong> Ver video</strong>
                    </button>
                    
                </div>
                
            </div>

        </div>




        <div class="LogoDesktop screen-container">

            <div class="LogoDesktop__bgCircle">
                <img loading="lazy"  width="100%" src="/assets/bg-circle-1.png" alt="Fondo logo Glowing phoenix" srcset="">
            </div>

            <div class="LogoDesktop__logo">
                <img width="100%" src="/assets/logo-completo.webp" alt="Logo Glowing Phoenix banda de metal" srcset="">
            </div>

            <div class="LogoDesktop__text">
                <p>nete a esta experiencia de otro mundo y haz un viaje metalero con nuestro hit musical</p>
            </div>

            <div class="LogoDesktop__button">

                <div class="d-grid gap-2">
                    <button class="btn btn-danger  my-btn-index watch-video-now" type="button">
                        <strong> Ver video</strong>
                    </button>
                    
                </div>
                
            </div>
        </div>

    `;
}

export const JsLogo = () =>{

    const btnsWatchVideo = document.getElementsByClassName('watch-video-now');
    for(let i=0; i< btnsWatchVideo.length;i++){

        btnsWatchVideo[i].addEventListener('click',()=>{

                window.location.href = 'https://www.youtube.com/watch?v=URSS6yOqRi0';

        });
    }
}