export const JsContainerInicio = ()=>{

    const MainVideo = document.getElementById('main-video');
    const videoSecond = document.getElementsByClassName('videosSecond__video');
    const videosSecond = [

        "/assets/videos-inicio/toque-2.mp4",
        "/assets/videos-inicio/toque-1.mp4",
        "/assets/videos-inicio/yisus.mp4",
        "/assets/videos-inicio/tomine-grupo-1.mp4"
        

    ];

    for(let f=0;f<videoSecond.length;f++){

        videoSecond[f].addEventListener('click',()=>{

            MainVideo.innerHTML = "";
            MainVideo.insertAdjacentHTML('afterbegin',`<video width="101%" controls src="${videosSecond[f]}"></video>`)

        });
    }

    const btnBrochure = document.getElementById('download-brochure');
    btnBrochure.addEventListener('click',()=>window.open('/assets/broshure-final.pdf', '_blank'));

    const ListenAlbum = document.getElementById('escuchar-album');
    ListenAlbum.addEventListener('click',()=>window.location.href="/musica");
}